@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
  background-color: v.$azul;
  margin-top: 5rem;
  padding-top: 3rem;
  height: 100%;
  color: v.$blanco;

  &__grid {
    @include m.contenedor;
    @include m.tablet {
      @include m.grid( 2, 3rem );
    }
  }

  &__logo {
    @include m.logo;
    @include m.texto-fluido( 1rem, 2rem );
  }

  &__texto {
    line-height: 2;
    text-align: center;
    @include m.tablet {
      text-align: left;
    }
  }

  &__copyright {
    background-color: darken( v.$azul, 10% );
    margin: 0;
    padding: 1rem 0;
    text-align: center;
    font-size: 1.4rem;
    font-weight: v.$black;

    &--regular {
      font-weight: v.$regular;
    }
  }
}